import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip";
import { usePathname } from 'next/navigation';
import Link from 'next/link';
import {
  LayoutDashboard,
  Users,
  Calendar,
  BookOpenText,
  FileText,
  LogOut,
  Settings,
  LifeBuoy,
  Menu, // Import Menu icon for mobile toggle
} from "lucide-react";
import { useState } from 'react';
import { useMediaQuery } from '@/hooks/use-media-query'; // Assuming you have this hook

// Define the structure for navigation items
interface NavItem {
  href: string;
  label: string;
  icon: React.ElementType;
  roles: ("STUDENT" | "TUTOR")[]; // Roles that can see this item
}

// Define navigation items
const navItems: NavItem[] = [
  { href: "/dashboard", label: "Dashboard", icon: LayoutDashboard, roles: ["STUDENT", "TUTOR"] },
  { href: "/dashboard/sessions", label: "Sessions", icon: Calendar, roles: ["STUDENT", "TUTOR"] },
  { href: "/dashboard/tutor/students", label: "My Students", icon: Users, roles: ["TUTOR"] },
  { href: "/dashboard/student/tutors", label: "My Tutors", icon: Users, roles: ["STUDENT"] },
  { href: "/dashboard/exercises", label: "Exercises", icon: BookOpenText, roles: ["STUDENT", "TUTOR"] },
  { href: "/dashboard/documents", label: "Documents", icon: FileText, roles: ["STUDENT", "TUTOR"] },
];

const bottomNavItems: NavItem[] = [
  { href: "/dashboard/settings", label: "Settings", icon: Settings, roles: ["STUDENT", "TUTOR"] },
  { href: "/dashboard/help", label: "Help", icon: LifeBuoy, roles: ["STUDENT", "TUTOR"] },
];

export function Sidebar({ currentRole }: { currentRole: "STUDENT" | "TUTOR" }) {
  const pathname = usePathname();
  const [isCollapsed, setIsCollapsed] = useState(true); // Default to collapsed
  const isDesktop = useMediaQuery("(min-width: 768px)");

  const toggleSidebar = () => setIsCollapsed(!isCollapsed);

  // Filter nav items based on the current role
  const getFilteredNavItems = (items: NavItem[]) => {
    return items.filter(item => {
      const baseHref = `/${item.href.split('/')[1]}`; // e.g., /dashboard
      const roleSpecificPath = item.href.startsWith(`/dashboard/${currentRole.toLowerCase()}`);

      // General dashboard item
      if (item.href === '/dashboard') return true;

      // Role-specific root links (e.g., /dashboard/tutor/students)
      if (roleSpecificPath) return true;

      // Shared links (check if the *base* path matches the start of current pathname)
      if (!roleSpecificPath && item.roles.includes(currentRole) && pathname.startsWith(baseHref)) {
         return true;
      }

      // Fallback for shared links where the direct href might match but isn't role-specific
      // (This handles cases like /dashboard/sessions, /dashboard/exercises, etc.)
      if (item.roles.includes(currentRole) && !item.href.startsWith('/dashboard/student') && !item.href.startsWith('/dashboard/tutor')) {
          return true;
      }

      return false;
    });
  };

  const filteredNavItems = getFilteredNavItems(navItems);
  const filteredBottomNavItems = getFilteredNavItems(bottomNavItems);

  const getAdjustedHref = (href: string) => {
    // Prepend role if the link is NOT already role-specific
    if (href.startsWith('/dashboard/') &&
        !href.startsWith('/dashboard/student') &&
        !href.startsWith('/dashboard/tutor') &&
        href !== '/dashboard') {
      return `/dashboard/${currentRole.toLowerCase()}${href.substring('/dashboard'.length)}`;
    }
    return href;
  };

  // Dynamic class for the main aside element based on collapsed state
  const asideClasses = cn(
    "fixed inset-y-0 left-0 z-10 flex flex-col border-r transition-all duration-300 ease-in-out",
    "bg-sidebar text-sidebar-foreground border-sidebar-border", // Use sidebar-specific colors
    isCollapsed ? "w-16" : "w-60"
  );

  const navLinkClasses = (href: string) => cn(
    "flex items-center justify-start gap-3 rounded-lg px-3 py-2 transition-all",
    "text-sidebar-muted-foreground hover:text-sidebar-accent-foreground hover:bg-sidebar-accent/10", // Use sidebar colors
    {
      "bg-sidebar-accent/20 text-sidebar-accent-foreground font-semibold": pathname === href, // Active state using sidebar accent
    }
  );

  const navLinkLabelClasses = cn(
    "origin-left transition-opacity duration-200 ease-in-out",
    isCollapsed ? "opacity-0 w-0 h-0 pointer-events-none" : "opacity-100"
  );

  return (
    <aside className={asideClasses}>
      {/* Sidebar Toggle Button - visible on desktop */}
      {isDesktop && (
        <div className={cn(
          "h-16 flex items-center border-b px-4 shrink-0",
          "border-sidebar-border", // Use sidebar border
          isCollapsed ? "justify-center" : "justify-between"
        )}>
          <Link href="/dashboard" className={cn("font-bold text-lg", navLinkLabelClasses)}>
            Dashboard
          </Link>
          <Button
            variant="ghost"
            size="icon"
            className="rounded-full h-8 w-8"
          >
            <Menu className="h-4 w-4" />
          </Button>
        </div>
      )}

      {/* Sidebar content */}
      <div className="flex-1">
        {/* Sidebar navigation links */}
        <nav className="flex-1 space-y-1">
          {filteredNavItems.map((item) => {
            const Icon = item.icon;
            const finalHref = getAdjustedHref(item.href);
            const isActive = pathname === finalHref;

            return (
              <Tooltip key={item.label}>
                <TooltipTrigger asChild>
                  <Link
                    href={finalHref}
                    className={navLinkClasses(finalHref)}
                  >
                    <Icon className="h-5 w-5 shrink-0" />
                    <span className={navLinkLabelClasses}>{item.label}</span>
                  </Link>
                </TooltipTrigger>
              </Tooltip>
            );
          })}
        </nav>

        {/* Bottom navigation links */}
        <nav className="flex-1 space-y-1">
          {filteredBottomNavItems.map((item) => {
            const Icon = item.icon;
            const finalHref = getAdjustedHref(item.href);
            const isActive = pathname === finalHref;

            return (
              <Tooltip key={item.label}>
                <TooltipTrigger asChild>
                  <Link
                    href={finalHref}
                    className={navLinkClasses(finalHref)}
                  >
                    <Icon className="h-5 w-5" />
                    <span className="sr-only">{item.label}</span>
                  </Link>
                </TooltipTrigger>
              </Tooltip>
            );
          })}
        </nav>
      </div>
    </aside>
  );
} 