import { useState, useEffect } from 'react';

/**
 * Custom hook for tracking the state of a CSS media query.
 *
 * @param {string} query - The media query string to watch (e.g., '(min-width: 768px)').
 * @returns {boolean} - Whether the media query currently matches.
 */
export function useMediaQuery(query: string): boolean {
  const [matches, setMatches] = useState<boolean>(() => {
    // Get initial value on the client, default to false on server
    if (typeof window !== 'undefined') {
      return window.matchMedia(query).matches;
    }
    return false; // Default value for SSR
  });

  useEffect(() => {
    // Ensure window is defined (runs only on client)
    if (typeof window === 'undefined') {
      return;
    }

    const mediaQueryList = window.matchMedia(query);

    // Handler for changes in media query status
    const documentChangeHandler = () => {
      setMatches(mediaQueryList.matches);
    };

    // Add listener
    // Using addEventListener for modern browsers, with fallback for older ones
    try {
      mediaQueryList.addEventListener('change', documentChangeHandler);
    } catch (e) {
      // Fallback for older browsers
      mediaQueryList.addListener(documentChangeHandler); // Deprecated
    }

    // Set the initial state correctly once mounted on client
    setMatches(mediaQueryList.matches);

    // Cleanup listener on component unmount
    return () => {
      try {
        mediaQueryList.removeEventListener('change', documentChangeHandler);
      } catch (e) {
        // Fallback for older browsers
        mediaQueryList.removeListener(documentChangeHandler); // Deprecated
      }
    };
  }, [query]); // Re-run effect if query changes

  return matches;
} 